:root {
  --accent: #ffd541;
  --title-color: #151515;
  --default-color: #323232;
  --hover-purple: #7240b2;
  --default-bg: #343333;
  --white: #fff;
  --bg-gray: #f5f5f5;
  --logo-color: #234424;
  --subtitle-color: #151515cc;
  --shadow-color: #ffffff80;
  --error-color: red;
  --transit: .3s ease-in-out;
}

