body {
  letter-spacing: .02em;
  color: var(--default-color);
  background-color: var(--white);
  flex-direction: column;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.67;
  display: flex;
}

.main {
  flex-grow: 1;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.nav-menu__btn {
  z-index: 2;
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  height: 42px;
  margin-right: 20px;
  display: none;
  position: relative;
}

@media (max-width: 980px) {
  .nav-menu__btn {
    display: flex;
  }
}

.nav-menu__btn:before, .nav-menu__btn:after {
  content: "";
  transition: all var(--transit);
  background-color: #e7e7e7;
  width: 36px;
  height: 2px;
  position: absolute;
  left: 0;
  right: 0;
  transform: rotate(180deg);
}

.nav-menu__btn span {
  transition: all var(--transit);
  background-color: #e7e7e7;
  width: 36px;
  height: 2px;
}

.nav-menu__btn:before {
  top: 7px;
}

.nav-menu__btn:after {
  bottom: 7px;
}

.nav-menu__btn.active:before {
  width: 15px;
  top: 15px;
  transform: rotate3d(0, 0, 1, -35deg);
}

.nav-menu__btn.active:after {
  width: 15px;
  bottom: 15px;
  transform: rotate3d(0, 0, 1, 35deg);
}

.nav-menu__btn.active span {
  transform: rotate(180deg);
}

.logo {
  color: var(--white);
  transition: all var(--transit);
  align-items: center;
  width: 100%;
  font-family: Montserrat, sans-serif;
  font-size: clamp(21px, 4vw, 40px);
  font-weight: 600;
  display: flex;
}

.logo-link {
  align-items: center;
  display: flex;
}

.logo-img {
  transition: all var(--transit);
  width: clamp(35px, 4vw, 60px);
  margin-right: 10px;
}

.animated-element {
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, transform .3s ease-in-out;
}

.animated {
  opacity: 1;
  visibility: visible;
  animation-fill-mode: both;
}

.hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.scrollUpButton {
  background-color: var(--hover-purple);
  color: var(--white);
  cursor: pointer;
  z-index: 90;
  transition: transform var(--transit), background-color var(--transit);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  display: flex;
  position: fixed;
  bottom: 40px;
  right: 40px;
  transform: translate3d(0, 100px, 0);
}

.scrollUpButton:hover {
  background-color: #7240b299;
}

.buttonUpIcon {
  fill: var(--white);
  width: 16px;
  height: 14px;
}

.social-links {
  transition: transform var(--transit);
  column-gap: 15px;
  display: flex;
}

@media (max-width: 980px) {
  .social-links {
    background-color: var(--default-bg);
    z-index: 10;
    border-radius: 0 0 0 15px;
    flex-direction: column;
    row-gap: 10px;
    padding: 20px 8px;
    position: fixed;
    top: 74px;
    right: 0;
    transform: translateY(-135%);
    box-shadow: 0 0 22px -4px #0000002b;
  }
}

.social-links.active {
  transform: translateY(0);
}

.social-item {
  justify-content: center;
  align-items: center;
  height: 35px;
  display: flex;
}

.social-link {
  height: 100%;
  padding: 5px;
}

@media (max-width: 980px) {
  .social-link {
    padding: 5px 15px;
  }
}

.social-icon {
  transition: fill var(--transit);
  fill: #ffffff80;
}

.social-icon:hover {
  fill: var(--white);
}

.social-button {
  cursor: pointer;
  min-width: 42px;
  height: 42px;
  margin-left: auto;
  display: none;
  position: relative;
}

@media (max-width: 980px) {
  .social-button {
    display: block;
  }
}

.social-button span, .social-button:before, .social-button:after {
  background-color: var(--white);
  transition: all var(--transit);
  border-radius: 50%;
  width: 6px;
  height: 6px;
  position: absolute;
}

.social-button:before {
  content: "";
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.social-button:after {
  content: "";
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
}

.social-button span {
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.social-button.active span {
  transform: scale(.7)translate(-4px, -4px);
}

.social-button.active:before {
  transform: scale(.7)translate(-4px, 28px);
}

.social-button.active:after {
  transform: scale(.7)translate(-4px, -29px);
}

.header {
  transition: all var(--transit);
  padding: 35px 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media (max-width: 980px) {
  .header {
    background: var(--default-bg);
    border-bottom-color: var(--default-bg);
    z-index: 9999;
    padding: 16px 0;
    box-shadow: 0 0 15px #15151559;
  }
}

.header-sticky {
  background: var(--default-bg);
  border-bottom-color: var(--default-bg);
  z-index: 9999;
  padding: 16px 0;
  box-shadow: 0 0 15px #15151559;
}

.header-sticky.logo {
  font-size: clamp(20px, 4vw, 25px);
}

.header-sticky.logo-img {
  width: clamp(30px, 4vw, 40px);
  margin-right: 10px;
}

.header-inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav {
  align-items: center;
  width: 100%;
  display: flex;
}

.header-links-wrap {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 700px;
  margin-left: auto;
  display: flex;
}

.header-nav-links {
  transition: transform var(--transit);
  align-items: center;
  column-gap: 50px;
  margin-right: 30px;
  display: flex;
}

@media (max-width: 1200px) {
  .header-nav-links {
    column-gap: 32px;
  }
}

@media (max-width: 980px) {
  .header-nav-links {
    z-index: 8;
    background-color: var(--default-bg);
    border-right: 1px solid var(--default-bg);
    flex-direction: column;
    align-items: baseline;
    row-gap: 20px;
    width: 270px;
    height: 100%;
    padding: 20px 35px;
    position: fixed;
    top: 74px;
    left: 0;
    transform: translateX(-100%);
    box-shadow: 0 0 15px #15151559;
  }
}

.header-nav-links.active {
  transform: translateX(0);
}

.header-nav-item {
  width: 100%;
}

.header-nav-link {
  letter-spacing: .02em;
  text-transform: uppercase;
  color: var(--white);
  transition: color var(--transit);
  font-size: 15px;
  font-weight: 500;
  line-height: 1.2;
  display: block;
}

@media (max-width: 980px) {
  .header-nav-link {
    padding: 15px 0;
  }
}

.header-nav-link:hover, .header-nav-link:focus, .header-nav-link:active, .header-nav-link--active {
  color: var(--accent);
}

.header-nav-backdrop {
  z-index: 1;
  display: none;
  position: fixed;
  inset: 80px 0 0;
}

.header-nav-backdrop.active {
  display: block;
}

.hero {
  background-image: linear-gradient(#000000a6, #000000a6), url("bg-ps-main.181f8a80.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
}

.hero-inner {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.hero-title {
  text-transform: uppercase;
  color: var(--white);
  letter-spacing: .01em;
  text-shadow: 0 4px 12px #0000004d;
  margin-bottom: 40px;
  font-family: Montserrat, sans-serif;
  font-size: clamp(45px, 7vw, 75px);
  font-weight: 900;
  line-height: 1.2;
  animation-delay: .3s;
  position: relative;
}

.hero-title:before {
  content: "";
  background-color: var(--white);
  width: 62px;
  height: 3px;
  position: absolute;
  bottom: -19px;
  left: calc(50% - 30px);
}

.hero-subtitle {
  color: var(--white);
  letter-spacing: -.03em;
  font-size: 18px;
  font-weight: 400;
  animation-delay: 1s;
}

.appointment {
  background-color: var(--default-color);
  color: var(--white);
  padding: 60px 0;
}

.appointment-form {
  align-items: center;
  margin: 0 auto;
  display: flex;
}

@media (max-width: 1200px) {
  .appointment-form {
    flex-direction: column;
    gap: 20px;
    max-width: 500px;
  }
}

.appointment-label {
  text-transform: uppercase;
  flex-direction: column;
  width: 100%;
  height: 80px;
  padding: 0 12px;
  font-weight: 400;
  display: flex;
}

.appointment-input {
  color: var(--white);
  border: none;
  border-bottom: 1px solid #ffffff1a;
  outline: none;
  padding: 10px 16px 16px 0;
  font-size: 18px;
}

.appointment-input::placeholder {
  color: #fff;
  font-size: 18px;
}

.appointment-input .notvalid {
  border-color: #f5543f;
}

.appointment-submit-btn {
  background-color: var(--white);
  color: var(--default-color);
  text-transform: uppercase;
  letter-spacing: .002em;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: color var(--transit), background-color var(--transit), border-color var(--transit);
  border-color: #0000;
  width: 100%;
  max-width: 250px;
  margin-top: 12px;
  margin-left: 27px;
  padding: 18px 35px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
  overflow: hidden;
}

@media (max-width: 1200px) {
  .appointment-submit-btn {
    margin-top: 45px;
    margin-left: 0;
  }
}

.appointment-submit-btn:hover, .appointment-submit-btn:focus, .appointment-submit-btn:active {
  color: var(--white);
  background-color: var(--hover-purple);
}

.featured {
  text-align: center;
  padding: 115px 0;
}

.featured-title {
  text-transform: uppercase;
  color: var(--title-color);
  letter-spacing: 0;
  margin-bottom: 18px;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  line-height: 1.2;
}

.featured-subtitle {
  color: var(--subtitle-color);
  max-width: 670px;
  margin: 0 auto 78px;
  font-size: 15px;
}

.featured-services {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.featured-item:hover .featured-img_wrap:before {
  opacity: 1;
}

.featured-item:hover .featured-btn {
  opacity: 1;
  transform: translate(-50%, 65%);
}

.featured-img_wrap {
  max-width: 370px;
  height: 390px;
  display: flex;
  position: relative;
}

.featured-img_wrap:before {
  content: "";
  opacity: 0;
  transition: opacity var(--transit);
  background: linear-gradient(#15151500 38.12%, #1515155d 100%);
  position: absolute;
  inset: 0;
}

.featured-img {
  object-fit: cover;
  flex: 1;
}

.featured-btn {
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--hover-purple);
  opacity: 0;
  transition: all var(--transit);
  padding: 12px 25px;
  font-size: 13px;
  font-weight: 500;
  position: absolute;
  top: 62%;
  left: 50%;
  transform: translateX(-50%);
}

.featured-btn:hover {
  color: var(--hover-purple);
  background-color: var(--white);
}

.featured-title_link {
  text-transform: uppercase;
  color: var(--title-color);
  margin-bottom: 10px;
  font-family: Montserrat, sans-serif;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.18;
  display: block;
}

.featured-text {
  letter-spacing: .05em;
  letter-spacing: .05em;
  text-transform: uppercase;
  color: var(--hover-purple);
  font-family: Montserrat, sans-serif;
  font-weight: 500;
  line-height: 1.14;
}

.featured-name {
  padding: 25px;
}

.promotion {
  text-align: center;
  color: var(--white);
  background-image: linear-gradient(#000000a6, #000000a6), url("rednails-bg.e9cdabf3.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  padding: 135px 0;
}

.promotion-inner {
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.promotion-subtitle {
  font-family: Montserrat, sans-serif;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.18;
}

.promotion-title {
  margin-bottom: 30px;
  font-family: Montserrat, sans-serif;
  font-size: 55px;
  font-weight: 700;
  line-height: 1.56;
}

.promotion-link {
  background-color: var(--white);
  letter-spacing: .002em;
  color: var(--default-color);
  transition: color var(--transit), background-color var(--transit);
  width: 190px;
  padding: 19px 35px;
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
}

.promotion-link:hover, .promotion-link:focus, .promotion-link:active {
  color: var(--white);
  background-color: var(--hover-purple);
}

.portfolio {
  text-align: center;
  padding: 110px 0 120px;
}

.portfolio-title {
  color: var(--title-color);
  text-transform: uppercase;
  letter-spacing: 0;
  margin-bottom: 30px;
  font-family: Montserrat, sans-serif;
  font-size: 45px;
  line-height: 1.2;
}

.portfolio-subtitle {
  letter-spacing: .05em;
  color: var(--subtitle-color);
  max-width: 670px;
  margin: 0 auto 78px;
  font-size: 15px;
  font-weight: 400;
}

.portfolio-list {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.portfolio-item {
  width: calc(25% - 15px);
  height: 320px;
  display: flex;
  position: relative;
}

.portfolio-item:hover .portfolio-link {
  opacity: 1;
  transform: translate(-50%, -50%);
}

.portfolio-img {
  object-fit: cover;
  flex: 1;
}

.portfolio-link {
  color: var(--title-color);
  background-color: var(--white);
  opacity: 0;
  transition: all var(--transit);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 85px;
  height: 85px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.46;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-70%, -50%);
  box-shadow: 0 0 0 10px #fff6;
}

.portfolio-link:hover {
  box-shadow: 0 0 0 20px #fff6;
}

.about {
  background-color: var(--default-color);
  color: var(--white);
  z-index: 1;
  padding: 135px 0;
  position: relative;
}

.about-inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media (max-width: 980px) {
  .about-inner {
    flex-direction: column;
  }
}

.about-img_wrap {
  width: 483px;
  height: 400px;
  display: flex;
  position: relative;
}

@media (max-width: 980px) {
  .about-img_wrap {
    margin-bottom: 50px;
  }
}

